.page-header {
    padding-top: 4px;
    background-color: $color-main-bg;
    @include tablet {
        padding-top: 32px;
    }
    @include desktop {
        padding-top: 37px;
    }
}
.container--flex {
    @include desktop {
    display: flex;
    justify-content: center;
    align-items: center;
    }
    
}
.page-header__container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo {
    display: block;
}

.mobile-menu-open {
    @include desktop-close {
        display: flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;
            margin-left: auto;
            border: none;
            background-color: transparent;
    }
    @include desktop {
        display: none;
    }
}
.mobile-menu-open__icon {
fill: $color-accent;
}
.menu {
    @include desktop-close {
//    display: none;
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    height: 100vh;
    
    background-color: #E17992;
    // transform: translateX(101%);
    // transition: transform 250ms linear;
    &.is-open {
        // transform: translateX(0);
        display: block;
    }
    @media screen and (min-width:768px) and (max-width:1199px) {
        width: 254px;
    }
    }
    @include desktop {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
.menu__container {
    display: flex;
    justify-content: space-between;
    height: 100%;
    overflow-y: scroll;
    @include desktop-close {
        padding: 20px;
flex-direction: column;
    }
       @include desktop {
        justify-content: center;
        align-items: center;
        flex-direction: row-reverse;
       }
}
.mobile-menu-close {
    margin-left: auto;
    margin-bottom: 24px;
    width: 32px;
    height: 32px;
    background-color: transparent;
    border: none;
    @include desktop {
            display: none;
        }
}

.mobile-menu-close__icon {
    fill: #fff;
    
}
.button-buy-tablet {
        display: none;
    @media screen and (min-width:768px) and (max-width:1199px) {
display: flex;
align-items: center;
justify-content: center;
margin-left: 32px;
width: 160px;
height: 44px;
padding-left: 37.23px;
padding-right: 37.23px;
background-color: $color-white;
border: none;
border-radius: 22px;
@include font(700, 16px, 21px, null);
transition: background-color 250ms linear;

    &:hover,
    &:focus {
        background-color: $color-button-hover;
    }
}

}
.button-buy-tablet__text {
    display: flex;
        justify-content: center;
        align-items: center;
        height: 44px;
        width: 69.43px;
        color: $color-accent;

       
}
.button-buy {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 33px;
    width: 209px;
    height: 44px;
    padding-left: 48.64px;
    padding-right: 48.64px;
    background-color: $color-white;
    border: none;
    border-radius: 22px;
    @include font(700, 16px, 21px, null);
    transition: background-color 250ms linear;
    
    &:hover,
    &:focus {
        background-color: $color-button-hover;
    }
    @include tablet {
     width: 186px;
     padding-left: 43.28px;
     padding-right: 43.28px;
    }
    @include desktop {
        width: 159px;
        padding-left: 37px;
        padding-right: 37px;
        margin-bottom: 0;
        margin-left: 141px;
    }
}

.button-buy__text {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 44px;
    width: 90.7px;
    color: $color-accent;
    @include tablet {
     width: 80.72px;
    }
    @include desktop {
        width: 69px;
    }
}

.button-buy__icon {
    fill: $color-accent;
    @include tablet {
           width: 18px;
        }
        @include desktop {
            width: 16px;
        }
}

.page-header__nav {

}

.page-header__list {
@include desktop {
    display: flex;
    margin-left: 128px;
}
}

.page-header__items {
   @include desktop-close {
    position: relative;
    
        &:not(:last-child) {
            margin-bottom: 23px;
        }
&::before {
        content: " ";
        position: absolute;
        top: 0;
        left: 0;
        height: 1px;
        width: 254px;
        background-color: #E18298;
        transform: translate(-20px, - 11.5px);
    }

    &:last-child {
        &::after {
            content: " ";
            position: absolute;
            top: 0;
            left: 0;
            height: 1px;
            width: 254px;
            background-color: #E18298;
            transform: translate(-20px, 30px);
        }
    }
   }
  @include desktop {
    &:not(:first-child) {
        margin-left: 35px;
    }
  }
}

.page-header__link {
    display: block;
    @include font(700, 14px, 18px, null);
    font-style: normal;
    color: $color-white;
    transition: color 250ms linear;

     &:hover,
     &:focus {
        color: $color-accent;
     }
}