#btn-prev {
    position: absolute;
    left: 0;
    top: 0;
    height: 50px;
    width: 50px;
    background-color: #C7133E;
    opacity: 0.4;
    cursor: pointer;
    transition: opasity .2s ease-in-out;
}

#btn-next {
    position: absolute;
    left: auto;
    right: 0;
    top: 0;
    height: 50px;
    width: 50px;
    background-color: #C7133E;
    opacity: 0.4;
    cursor: pointer;
    transition: opasity .2s ease-in-out;
}




