.button-block {
    width: 100%;
    height: 50px;
}

.dot {
    background: #D9D9D9;
    width: 12px;
    height: 12px;
    border-radius: 50%;

}

.dot:not(last-child) {
    margin-right: 18px;
}

.dot:last-child {
    margin-right: 0;
}

.dot .active {
    background-color: white
}

.dot__current {
    width: 100%;
    height: 100%;
}